.wrapper {
    border-top: 1px solid #1618231f;
    }
    
    .label {
        padding: 8px;
        font-size: 1.4rem;
        font-weight: 600;
    }
    
    .account-item {
        display: flex;
        padding: 8px;
        cursor: pointer;
    }
    
    .avatar {
        width: 32px;
        height: 32px;
        border-radius: 50%;
        object-fit: cover;
    }
    
    .item-info {
        flex: 1;
        margin-left: 12px;
    }
    
    .nickname {
        font-size: 1.6rem;
        color: #161823;
        line-height: 1;
    }
    
    .check {
        margin-left: 6px;
        font-size: 1.4rem;
        color: #20d5ec;
    }
    .name {
        font-size: 1.2rem;
        color: #161823bf;
    }
    
    .more-btn{
        padding: 8px;
        font-weight: 700;
        font-size: 1.4rem;
        color: var(--primary);
        cursor: pointer;
    }